import { E_AUTH, E_CONNECTIVITY, E_UNEXPECTED, E_USER } from 'lib-bb-model-errors';
import { getColorset, getColorClassByIndex, getColorByIndex } from 'lib-wm-colorset';
import composeSelectors from 'lib-wm-compose-selectors';

const POPOVER_SHIFT = 20;

/**
 * @description
 * Map of error code to message, title and icon to display to user
 *
 * @public
 * @name errors
 * @type {object.<E_AUTH | E_CONNECTIVITY | E_UNEXPECTED | E_USER, errorMessageDescriptor>}
 */
const errors = {
  [E_AUTH]: {
    message: 'model.error.auth.message',
    title: 'model.error.auth.title',
    icon: 'fa-sign-in-alt',
  },
  [E_CONNECTIVITY]: {
    message: 'model.error.connectivity.message',
    title: 'model.error.connectivity.title',
    icon: 'fa-exclamation-triangle',
  },
  [E_USER]: {
    message: 'model.error.user.message',
    title: 'model.error.user.title',
    icon: 'fa-user',
  },
  [E_UNEXPECTED]: {
    message: 'model.error.unexpected.message',
    title: 'model.error.unexpected.title',
    icon: 'fa-server',
  },
};

export default ({ stateContainer, $filter }) => {
  const numberFilter = $filter('number');

  /**
   * @description
   * Sets styling and content for custom tooltip
   *
   * @name getCustomTooltip
   * @type {function}
   * @param {object} tooltipModel Chartjs model of selected item
   * @param {object} tooltipWrap Tooltip element link
   * @param {array} series Array of chart data
   * @returns {void}
   */
  const getCustomTooltip = (tooltipModel, tooltipWrap, series) => {
    const tooltipEl = tooltipWrap.querySelector('.popover');
    const arrow = tooltipEl.querySelector('.arrow');

    if (tooltipModel.opacity === 0) {
      tooltipEl.classList.remove('show');
      if (!tooltipEl.classList.contains('d-none')) {
        tooltipEl.classList.add('d-none');
      }
      return;
    }

    const index = tooltipModel.dataPoints[0].index;
    const item = series[index];

    const titleWrapper = tooltipWrap.querySelector('.popover-title-content');
    titleWrapper.innerHTML = item.name;

    const circleWrapper = tooltipWrap.querySelector('.popover-title-circle');
    circleWrapper.style.color = getColorByIndex(index);

    const valuationCurrency = tooltipWrap.querySelector('.popover-currency');
    valuationCurrency.innerHTML = item.valuation.currencyCode;

    const valuationWrapper = tooltipWrap.querySelector('.popover-valuation');
    valuationWrapper.innerHTML = numberFilter(item.valuation.amount, 2);

    const percentageWrapper = tooltipWrap.querySelector('.popover-percentage');
    percentageWrapper.innerHTML = `${numberFilter(item.value, 2)}%`;

    const rangesWrapper = tooltipWrap.querySelector('.popover-ranges');
    const rangeValuesWrapper = tooltipWrap.querySelector('.popover-range');
    if (!item.min && !item.max) {
      Object.assign(rangesWrapper.style, { display: 'none' });
    } else {
      Object.assign(rangesWrapper.style, { display: 'block' });
      rangeValuesWrapper.innerHTML = `${numberFilter(item.min)}% - ${numberFilter(item.max)}%`;
    }

    tooltipEl.classList.remove('d-none');

    Object.assign(tooltipEl.style, {
      left: `${tooltipModel.caretX + POPOVER_SHIFT}px`,
      top: `${tooltipModel.caretY}px`,
      transform: 'translateY(-50%)',
    });
    Object.assign(arrow.style, {
      top: `${(tooltipEl.offsetHeight / 2)}px`,
      transform: 'translateY(-50%)',
    });
    tooltipEl.classList.add('show');
  };

  /**
   * @description
   * Describes predefined options for radar chart
   *
   * @name radarOptions
   * @type {object}
   */
  const radarOptions = {
    tooltips: {
      enabled: false,
      mode: 'nearest',
      custom: getCustomTooltip,
    },
  };

  /**
   * @description
   * Selector for allocations
   *
   * @private
   * @name allocationsSelector
   * @type {function}
   * @returns {array} Array of allocations received from state
   */
  const allocationsSelector =
    stateContainer.createSelector(state => state.allocations.data.allocations);

  /**
   * @description
   * Gets predefined options for radar chart dataset
   *
   * @name getRadarDatasetOptions
   * @type {function}
   * @returns {object} Radar dataset options
   */
  const getRadarDatasetOptions = composeSelectors(
    allocationsSelector,
    (data) => ({ pointBackgroundColor: getColorset(data.length) })
  );

  /**
   * @description
   * Checks if allocations' data is available
   *
   * @name isAllocationsAvailable
   * @type {function}
   * @returns {boolean} returns true if allocations data is present
   */
  const isAllocationsAvailable = composeSelectors(
    allocationsSelector,
    data => data.length > 0
  );

  /**
   * @description
   * Compares allocations % value
   *
   * @name allocationsComparator
   * @type {function}
   * @returns {number} return positive or negative number
   */
  const allocationsComparator = (item1, item2) => item2.value - item1.value;

  /**
   * @description
   * Gets allocations data and transforms it for spiderweb chart
   *
   * @name getAllocations
   * @type {function}
   * @returns {array} Allocations' data
   */
  const getAllocations = composeSelectors(
    allocationsSelector,
    (data) =>
      data
        .map((allocation) => ({
          name: allocation.name,
          min: allocation.allocationMinPct,
          max: allocation.allocationMaxPct,
          value: allocation.allocationPct,
          valuation: allocation.valuation,
        }))
        .slice()
        .sort(allocationsComparator)
  );

  /**
   * @description
   * Gets predefined options for radar chart
   *
   * @name getRadarOptions
   * @type {function}
   * @returns {object} Radar options
   */
  const getRadarOptions = () => radarOptions;

  /**
   * @description
   * Checks if asset-classes is selected
   *
   * @name isClass
   * @type {function}
   * @returns {boolean}
   */
  const isClass = stateContainer.createSelector(state => state.route.name === 'asset-classes');

  /**
   * @description
   * Returns error object by error code
   *
   * @public
   * @name getError
   * @type {Object}
   * @returns {errorMessageDescriptor} Descriptor of the error
   */
  const getError = (code) => errors[code];

  return {
    isAllocationsAvailable,
    getRadarOptions,
    getAllocations,
    getRadarDatasetOptions,
    isClass,
    getError,
    getColorClassByIndex,
  };
};



